}
/*******************************************************
                          WORK BOX
*******************************************************/
.work-container{
  height : 100%;
}
.border{
  border : 20px solid #fff;
  border-radius : 5px;
}
.work-box{
  text-align : center;
  height: 180px;
  overflow : hidden;
  position : relative;
  box-sizing : border-box;
  margin : 20px;
  vertical-align : middle;
}
a{
  color: #666;
}
a:hover{
  color: #464646;
  text-decoration: none;
}
a:focus{
  color: #464646;
  text-decoration: none;
}
.work-box .move{
  position : absolute;
  width : 30%;
  height : 70px;
  z-index : 1;
  bottom : 0;
  padding:0 5px;
  text-align:left;
}
.work-box .move:last-child{
  text-align: right;
}

.work-box .move .fa{
  color: rgba(0, 0, 0, 0.5);
  font-weight: bold;
  font-size: 3em;
  margin: 17px 0;
}
.work-box .move:hover .fa{
  color: rgba(255, 136, 25, 0.8);
}

.work-box .move-left{
  left : 0px;
}
.work-box .move-right{
  right : 0px;
}
.work-box .work-img{
  display : block;
  overflow : hidden;
  position : absolute;
  bottom : 0px;
  left : 0px;
}
.work-box .items{
  display : flex;
  flex-flow : row nowrap;
}
.work-box .items img{
  height : 70px;
  margin-right : 5px;
}
.work-box .items img:last-child{
margin-right : 0px;
}


/*******************************************************
                      WORK DETAIL
*******************************************************/
.grid {
	max-width: 69em;
	list-style: none;
	margin: 20px auto;
	padding: 0;
}

.grid li {
	display: block;
	float: left;
	padding: 7px;
	width: 33%;
	opacity: 0;
}

.grid li.in-view,
.no-js .grid li,
.no-cssanimations .grid li {
	opacity: 1;
}

.grid li a,
.grid li img {
	outline: none;
	border: none;
	display: block;
  margin: auto;
}
.grid li img{
	max-width: 95%;
  border-radius: 10px;
}

/* Effect : fall perspective */
.grid.effect {
	-webkit-perspective: 1300px;
	perspective: 1300px;
}

.grid.effect li.in-view {
	-webkit-transform-style: preserve-3d;
	transform-style: preserve-3d;
	-webkit-transform: translateZ(400px) translateY(300px) rotateX(-90deg);
	transform: translateZ(400px) translateY(300px) rotateX(-90deg);
	-webkit-animation: fallPerspective .8s ease-in-out forwards;
	animation: fallPerspective .8s ease-in-out forwards;
}

@-webkit-keyframes fallPerspective {
	0% { }
	100% { -webkit-transform: translateZ(0px) translateY(0px) rotateX(0deg); opacity: 1; }
}

@keyframes fallPerspective {
	0% { }
	100% { -webkit-transform: translateZ(0px) translateY(0px) rotateX(0deg); transform: translateZ(0px) translateY(0px) rotateX(0deg); opacity: 1; }
}

