/*********************  CAPTION **********************/
.captionBox{
  overflow: hidden;
  position: relative;
  padding: 7px 0;
  /*background-color: #EEE;*/
}
.captionBox .caption {
    background-color: rgba(0,0,0,0.8);
    position: absolute;
    margin: auto;
    color: #fff;
    -webkit-transition: all 300ms ease-out;
    -moz-transition: all 300ms ease-out;
    -o-transition: all 300ms ease-out;
    -ms-transition: all 300ms ease-out;
    transition: all 300ms ease-out;
    left: 0;
    right:0;
    opacity:0;
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    border-radius: 10px;
}

.captionBox .full-caption {
    text-align: left;
    padding: 10px 10px;
    text-align: center;
    overflow:hidden;
}
.captionBox .caption h4{
}
.captionBox .caption p{
  font-size: 0.9em;
  font-style: italic;
}
.captionBox a .shadow-img{
  -webkit-box-shadow: 3px 3px 8px 1px rgba(0, 0, 0, 0.2);
  box-shadow: 3px 3px 8px 1px rgba(0, 0, 0, 0.2);
}
.captionBox a:hover .shadow-img{
  -webkit-box-shadow: 3px 3px 8px 1px rgba(0, 0, 0, 0.4);
  box-shadow: 3px 3px 8px 1px rgba(0, 0, 0, 0.4);
}
